import { ChangeDetectionStrategy, Component, EventEmitter, Input, Output } from '@angular/core';

/**
 * @docsPrivate
 *
 * @description
 *
 * Componente que constri cada item no dropdown, contendo o checkbox e o label.
 */
@Component({
  selector: 'po-multiselect-item',
  templateUrl: './po-multiselect-item.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class PoMultiselectItemComponent {
  /** Rtulo do item. */
  @Input('p-label') label: string;

  /** Esta propriedade indica se o campo est selecionado ou no. */
  @Input('p-selected') selected?: boolean = false;

  /** Evento que ser disparado toda vez que o usurio marcar ou desmarcar um item. */
  @Output('p-change') change = new EventEmitter();

  itemClicked() {
    this.selected = !this.selected;
    this.change.emit(this.selected);
  }
}
