import { PipeTransform } from '@angular/core';

/**
 * @description
 *
 * O pipe po-i18n  responsvel por tratar literais parmetrizadas, dando mais flexibilidade as literais de traduo.
 * O nmero de parmetros inseridos nas literais deve coincidir com a quantia de parmetros passados por parmetro,
 * os parmetros sero substituidos de acordo com a ordem informada.
 *
 * Para inserir um parmetro em uma literal, o mesmo dever ser inserido entre chaves dentro da literal e posicionado
 * de acordo como deve ser exibido aps a sua transformao.
 *
 * ```
 * const i18nPT = {
 *   pagination: 'Pgina {1} de {2} pginas.',
 *   totalPages: 'Total de {totalPages} encontradas.'
 * };
 * ```
 *
 *  possvel passar um valor ou um array de valores para o pipe, caso seja passado um array, os valores devem obedecer a ordem
 * informada na literal.
 *
 * ```
 * {{ i18nPT.pagination | poI18n:[1,10] }}
 * {{ i18nPT.totalPages | poI18n:10 }}
 * ```
 */
export class PoI18nBasePipe implements PipeTransform {
  transform(value: string, args: any): string {
    if (!value) {
      return '';
    }

    if (!(args instanceof Array)) {
      args = [args];
    }

    for (const arg of args) {
      value = value.replace(/(\{\w*\})+/, arg);
    }

    return value;
  }
}
