import { EventEmitter, Input, Output, Directive } from '@angular/core';

import { convertToBoolean } from '../../utils/util';
import { PoColorPaletteEnum } from '../../enums/po-color-palette.enum';

import { PoTagItem } from './interfaces/po-tag-item.interface';
import { PoTagOrientation } from './enums/po-tag-orientation.enum';
import { PoTagType } from './enums/po-tag-type.enum';

const poTagColors = (<any>Object).values(PoColorPaletteEnum);
const poTagOrientationDefault = PoTagOrientation.Vertical;

/**
 * @description
 *
 * Este componente permite exibir um valor em forma de um marcador colorido, sendo possvel definir uma legenda e realizar customizaes
 * na cor, iconografia e tipo.
 *
 * Alm disso,  possvel definir uma ao que ser executada tanto ao *click* quanto atravs das teclas *enter/space* enquanto navega
 * utilizando a tecla *tab*.
 *
 * Seu uso  recomendado para informaes que necessitem de destaque em forma de marcao.
 */
@Directive()
export class PoTagBaseComponent {
  private _color?: string;
  private _icon?: boolean | string;
  private _inverse?: boolean;
  private _orientation?: PoTagOrientation = poTagOrientationDefault;
  private _type?: PoTagType;

  public readonly poTagOrientation = PoTagOrientation;

  /**
   * @optional
   *
   * @description
   *
   * Define uma cor para a *tag*.
   *
   * Valores vlidos:
   *  - <span class="dot po-color-01"></span> `color-01`
   *  - <span class="dot po-color-02"></span> `color-02`
   *  - <span class="dot po-color-03"></span> `color-03`
   *  - <span class="dot po-color-04"></span> `color-04`
   *  - <span class="dot po-color-05"></span> `color-05`
   *  - <span class="dot po-color-06"></span> `color-06`
   *  - <span class="dot po-color-07"></span> `color-07`
   *  - <span class="dot po-color-08"></span> `color-08`
   *  - <span class="dot po-color-09"></span> `color-09`
   *  - <span class="dot po-color-10"></span> `color-10`
   *  - <span class="dot po-color-11"></span> `color-11`
   *  - <span class="dot po-color-12"></span> `color-12`
   *
   * > **Ateno:** A propriedade `p-type` sobrepe esta definio.
   */
  @Input('p-color') set color(value: string) {
    this._color = poTagColors.includes(value) ? value : undefined;
  }

  get color(): string {
    return this._color;
  }

  /**
   * @optional
   *
   * @description
   *
   * Define ou ativa um cone que ser exibido ao lado do valor da *tag*.
   *
   * > Veja os valores vlidos na [biblioteca de cones](guides/icons).
   *
   * Quando `p-type` estiver definida, basta informar um valor igual a `true` para que o cone seja exibido conforme descries abaixo:
   * - <span class="po-icon po-icon-ok"></span> - `success`
   * - <span class="po-icon po-icon-warning"></span> - `warning`
   * - <span class="po-icon po-icon-close"></span> - `danger`
   * - <span class="po-icon po-icon-info"></span> - `info`
   *
   * @default `false`
   */
  @Input('p-icon') set icon(value: boolean | string) {
    if (this.type) {
      this._icon = convertToBoolean(value);
    } else {
      this._icon = value;
    }
  }

  get icon(): boolean | string {
    return this._icon;
  }

  /**
   * @optional
   *
   * @description
   *
   * Ativa a inverso de cores configuradas no componente, possibilitando uma visualizao de status ativo e inativo.
   *
   * > A cor do texto, do cone e da borda ficam com a cor utilizada na propriedade `p-color` ou a cor correspondente ao `p-type`,
   * e a cor do fundo fica branca.
   *
   * @default `false`
   */
  @Input('p-inverse') set inverse(value: boolean) {
    this._inverse = convertToBoolean(value);
  }

  get inverse(): boolean {
    return this._inverse;
  }

  /**
   * @optional
   *
   * @description
   *
   * Define uma legenda que ser exibida acima ou ao lado da *tag*, de acordo com a `p-orientation`.
   */
  @Input('p-label') label?: string;

  /**
   * @optional
   *
   * @description
   *
   * Define o *layout* de exibio.
   *
   * @default `vertical`
   */
  @Input('p-orientation') set orientation(value: PoTagOrientation) {
    this._orientation = (<any>Object).values(PoTagOrientation).includes(value) ? value : poTagOrientationDefault;
  }

  get orientation(): PoTagOrientation {
    return this._orientation;
  }

  /**
   * @optional
   *
   * @description
   *
   * Define o tipo da *tag*.
   *
   * Valores vlidos:
   *  - `success`: cor verde utilizada para simbolizar sucesso ou xito.
   *  - `warning`: cor amarela que representa aviso ou advertncia.
   *  - `danger`: cor vermelha para erro ou aviso crtico.
   *  - `info`: cor cinza escuro que caracteriza contedo informativo.
   *
   * > Quando esta propriedade for definida, ir sobrepor a definio de `p-color` e `p-icon` somente ser exibido caso seja `true`.
   *
   * @default `info`
   */
  @Input('p-type') set type(value: PoTagType) {
    this._type = (<any>Object).values(PoTagType).includes(value) ? value : undefined;
  }

  get type(): PoTagType {
    return this._type;
  }

  /** Texto da tag. */
  @Input('p-value') value: string;

  /**
   * @optional
   *
   * @description
   *
   * Ao que ser executada ao clicar sobre o `po-tag` e que receber como parmetro um objeto contendo o seu valor e tipo.
   */
  @Output('p-click') click?: EventEmitter<any> = new EventEmitter<PoTagItem>();
}
