import { Directive, Input, TemplateRef } from '@angular/core';

/**
 * @usedBy PoListViewComponent
 *
 * @description
 *
 * Esta diretiva permite que sejam apresentadas informaes essenciais de cada item.
 *
 * Deve-se utilizar como parmetro a referncia do item e/ou ndice, sendo por padro o item.
 *  - Item: `item` determina o item da linha corrente.
 *  - ndice: `index` determina o ndice da linha corrente.
 *
 * Esta diretiva pode ser usada de duas formas: explcita ou *syntax sugar*. Veja a seguir ambos, respectivamente:
 *
 * ```
 * ...
 * <po-list-view
 *   p-property-title="name"
 *   [p-items]="[{id: 1, name: "Register", email: register@po-ui.com}]">
 *
 *   <ng-template p-list-view-content-template let-item let-code="index">
 *     <div class="po-row">
 *       <po-info class="po-md-6" p-label="Code" [p-value]="code"></po-info>
 *       <po-info class="po-md-6" p-label="Email" [p-value]="item.email"></po-info>
 *     </div>
 *   </ng-template>
 *
 * </po-list-view>
 *
 * ...
 * ```
 *
 * ```
 * ...
 * <po-list-view
 *    p-property-title="name"
 *    [p-items]="[{id: 1, name: "Register", email: register@po-ui.com}]">
 *
 *    <div *p-list-view-content-template="let item, let i=index" class="po-row">
 *      <po-info class="po-md-12" p-label="Email" [p-value]="item.email"></po-info>
 *    </div>
 * </po-list-view>
 * ...
 *
 * ```
 *
 * A diretiva **p-list-view-content-template**, possibilita tambm alterar o ttulo dos itens. Para isto,
 *  necessrio atribuir a referncia da funo que faz a alterao,  propriedade `p-title`,
 * a mesma deve retornar um valor do tipo *string*. Veja o exemplo a seguir:
 *
 * ```
 * ...
 * @Component({
 *    selector: 'app-root',
 *    template: `
 *      ...
 *      <po-list-view
 *        [p-items]="items">
 *        <ng-template p-list-view-content-template let-item [p-title]="customTitle">
 *          <div class="po-row">
 *            <po-info class="po-md-12" p-label="Customer" [p-value]="item.customer"></po-info>
 *          </div>
 *        </ng-template>
 *      ...
 *    `
 * })
 * export class AppComponent {
 *    public items = [{
 *      code: 1200,
 *      product: 'Rice',
 *      customer: 'Supermarket 1',
 *    }, {
 *      code: 1355,
 *      product: 'Bean',
 *      customer: 'Supermarket 2'
 *    }];
 *
 *    customTitle(item) {
 *      return `${item.code} - ${item.product}`;
 *    }
 * }
 * ```
 */
@Directive({
  selector: '[p-list-view-content-template]'
})
export class PoListViewContentTemplateDirective {
  /**
   * @optional
   *
   * @description
   *
   * Funo que deve retornar um valor do tipo `string`, que ser utilizado como o ttulo de cada item da lista.
   *
   * > Por parmetro ser enviado o item corrente, onde poder utiliz-lo para recuperar dados sobre o mesmo.
   */
  @Input('p-title') title: (item) => string;

  // Necessrio manter templateRef para o funcionamento do row template.
  constructor(public templateRef: TemplateRef<any>) {}
}
