/**
 * @usedBy PoTagComponent
 *
 * @description
 *
 * Define os tipos de orientaes disponveis para o `po-tag`.
 */
export enum PoTagOrientation {
  /** A tag ser exibida na horizontal, ao lado direito em relao ao label. */
  Horizontal = 'horizontal',

  /** Exibe a tag na vertical, ou seja, abaixo do label. */
  Vertical = 'vertical'
}
