import { Input, Directive } from '@angular/core';

/**
 * @description
 *
 * Este componente apresenta uma linha demarcadora de blocos e pode conter um *label*. Seu uso  indicado para definio
 * e organizao de informaes em uma tela e sua caracterstica  semelhante  tag `<hr>`.
 */
@Directive()
export class PoDividerBaseComponent {
  /** Valor do rtulo a ser exibido. */
  @Input('p-label') label?: string;
}
