import { Input, Directive } from '@angular/core';

import { PoBreadcrumbItem } from './po-breadcrumb-item.interface';

/**
 * @description
 *
 * Este componente gera uma estrutura de navegao que apresenta ao usurio a localizao
 * da URL atual, exibindo as antecessoras conforme  realizado a navegao na aplicao.
 *
 * Quando no houver espaamento suficiente para exibi-ls, o componente se encarrega tambm
 * de agrupar as URLs antecessoras, gerando assim um cone que permite a visualizao em cascata.
 *
 * Caso um endereo seja especificado na propriedade `p-favorite-service`, o componente permite ao usurio
 * favoritar a URL.
 *
 * Havendo necessidade de incluir parmetros na requisio do servio,
 * o componente dispe da propriedade `p-params-service` que recebe um objeto contendo as informaes.
 */
@Directive()
export class PoBreadcrumbBaseComponent {
  itemsView: Array<PoBreadcrumbItem> = [];
  private _items: Array<PoBreadcrumbItem> = [];

  /**
   * @optional
   *
   * @description
   *
   * Permite definir uma URL no componente `po-breadcrumb` para favoritar ou desfavoritar.
   * > Para utilizar esta propriedade, o ltimo `PoBreadcrumbItem` da lista de items da propriedade `p-items` deve ter um link informado.
   *
   * > A API deve estar preparada para retornar um objeto no formato `{ isFavorite: boolean }`.
   *
   * Ao iniciar, o `po-breadcrumb` faz um GET na URL definida na propriedade `p-favorite-service` e deve retornar a propriedade
   * `{ isFavorite: boolean }` do ltimo `PoBreadcrumbItem` definido na lista de itens da propriedade `p-items`.
   *
   * Ao clicar em favoritar ou desfavoritar o `po-breadcrumb` faz um POST com o link e a propriedade `{ isFavorite: boolean }`
   * definidos no ltimo item da propriedade `p-items`.
   *
   * > Caso algum parmetro seja definido na propriedade `p-params-service`, o mesmo ser enviado para a API e retornar
   * aps fazer um GET ou POST.
   *
   * Exemplo de URL contendo o servio de favoritar ou desfavoritar:
   *
   * ```
   * https://po-ui.io/sample/api/favorite
   * ```
   *
   * Ao fazer o GET o `po-breadcrumb` concatena o link com a URL de servio. Exemplo:
   *
   * ```
   * GET http://<domain>/api/favorite?url=/example
   * ```
   *
   * ```
   * GET http://po.com.br/sample/api/favorite?url=/example
   * ```
   *
   * ```
   * POST
   * payload: { isFavorite: true, url: '/example' }
   * ```
   *
   * Caso possua parmetros definidos na propriedade `p-params-service`:
   *
   * ```
   * POST
   * payload: { isFavorite: true, url: "/example", params: "{ id: 14, user: 'dev.po' }" }
   * ```
   *
   * Exemplos de retorno:
   *
   * ```
   * { isFavorite: true, url: "/example" }
   * ```
   *
   * ```
   * { isFavorite: false, url: "/example" }
   * ```
   *
   * ```
   * { isFavorite: false, url: "/example", params: "{ id: 14, user: 'dev.po' }" }
   * ```
   */
  @Input('p-favorite-service') favoriteService?: string;

  /**
   * @description
   *
   * Lista de itens do _breadcrumb_.
   *
   * **Exemplo:**
   * ```
   * { label: 'Po Portal', link: 'portal' }
   * ```
   */
  @Input('p-items') set items(items: Array<PoBreadcrumbItem>) {
    this._items = items;
    this.itemsView = [].concat(items);
  }

  get items() {
    return this._items;
  }

  /**
   * @optional
   *
   * @description
   *
   * Objeto que possibilita o envio de parmetros adicionais  requisio.
   */
  @Input('p-params-service') paramsService?: object;

  protected clickoutListener: () => void;

  protected resizeListener: () => void;
}
