import { Input, Directive } from '@angular/core';

import { convertToBoolean } from '../../utils/util';

/**
 * @description
 *
 * O componente `po-tabs`  responsvel por agrupar [abas](/documentation/po-tab) dispostas numa linha horizontal,
 * ideal para facilitar a organizao de contedos.
 *
 * Em dispositivos mveis o componente exibir tadas as abas de maneira linear gerando um *scroll* na horizontal. J em telas telas com
 * resolues maiores, o componente exibir at cinco abas normalmente, porm,  partir de seis abas o componente automaticamente torna
 * visvel apenas as quatro primeiras, agrupando as subsequentes na quinta aba rotulada de **Mais**.
 *
 * > As abas que estiverem agrupadas sero dispostas numa cascata suspensa que ser exibida ao clicar no boto **Mais**.
 *
 *  possvel realizar a navegao entre as abas atravs da tecla TAB do teclado.
 * Caso uma aba estiver desabilitada, no receber foco de navegao.
 *
 * #### Boas prticas
 *
 * - Evite utilizar uma quantidade excessiva de abas, pois ir gerar um *scroll* muito longo no `dropdown`;
 * - Evite `labels` extensos para as `tabs` pois podem quebrar seu *layout*, use `labels` diretos, curtos e intuitivos.
 */
@Directive()
export class PoTabsBaseComponent {
  private _small?: boolean = false;

  /**
   * @optinal
   *
   * @description
   *
   * Diminui o tamanho das abas.
   *
   * @default `false`
   */
  @Input('p-small') set small(value: boolean) {
    this._small = convertToBoolean(value);
  }

  get small(): boolean {
    return this._small;
  }
}
