/**
 * @usedBy PoDynamicFormComponent
 *
 * @description
 *
 * Enum para definio do tipo de campo que ser criado dinamicamente.
 */
export enum PoDynamicFieldType {
  /** Valor booleano. */
  Boolean = 'boolean',

  /** Valor numrico que contm casas decimais e milhar. */
  Currency = 'currency',

  /** Valor para data. */
  Date = 'date',

  /** Valor para data e hora. */
  DateTime = 'datetime',

  /** Utilizado para informar/exibir hora. */
  Time = 'time',

  /** Valor nmerico. */
  Number = 'number',

  /** Texto. */
  String = 'string'
}
