import { EventEmitter, Input, Output, Directive } from '@angular/core';

import { convertToBoolean } from '../../utils/util';

const PO_BUTTON_TYPES = ['default', 'primary', 'danger', 'link'];
const PO_BUTTON_TYPE_DEFAULT = 'default';

/**
 * @description
 *
 * O `po-button` permite que o usurio execute aes predefinidas pelo desenvolvedor.
 *
 * Atravs dos tipos,  possvel identificar a importncia de cada ao, sendo ela primria (`primary`) ou at mesmo uma
 * ao irreversvel (`danger`), como a excluso de um registro.
 *
 * #### Boas prticas
 *
 * - Evite `labels` extensos que quebram o layout do `po-button`, use `labels` diretos, curtos e intuitivos.
 * - Utilize apenas um `po-button` configurado como `primary` por pgina.
 * - Para aes irreversveis use sempre o tipo `danger`.
 */
@Directive()
export class PoButtonBaseComponent {
  private _disabled?: boolean = false;
  private _loading?: boolean = false;
  private _small?: boolean = false;
  private _type?: string = 'default';

  /**
   * @optional
   *
   * @description
   *
   * Label do boto.
   */
  @Input('p-label') label?: string;

  /**
   * @optional
   *
   * @description
   *
   * Exibe um cone de carregamento  esquerda do _label_ do boto.
   *
   * > Quando esta propriedade estiver habilitada, desabilitar o boto.
   *
   * @default `false`
   */
  @Input('p-loading') set loading(value: boolean) {
    this._loading = convertToBoolean(value);
  }

  get loading(): boolean {
    return this._loading;
  }

  /**
   * cone exibido ao lado esquerdo do label do boto.
   *
   *  possvel usar qualquer uma dos cones da [Biblioteca de cones](/guides/icons).
   */
  @Input('p-icon') icon?: string;

  /**
   * @optional
   *
   * @description
   *
   * Deixa o boto menor.
   *
   * @default `false`
   */
  @Input('p-small') set small(value: boolean) {
    this._small = <any>value === '' ? true : convertToBoolean(value);
  }
  get small(): boolean {
    return this._small;
  }

  /**
   * @optional
   *
   * @description
   *
   * Define o estilo do `po-button`.
   *
   * Valore vlidos:
   *  - `default`: estilo padro do `po-button`.
   *  - `primary`: deixa o `po-button` com destaque, deve ser usado para aes primrias.
   *  - `danger`: deve ser usado para aes que o usurio precisa ter cuidado ao executa-l.
   *  - `link`: o `po-button` recebe o estilo de um link.
   *
   * @default `default`
   */
  @Input('p-type') set type(value: string) {
    this._type = PO_BUTTON_TYPES.includes(value) ? value : PO_BUTTON_TYPE_DEFAULT;
  }
  get type(): string {
    return this._type;
  }

  /**
   * @optional
   *
   * @description
   *
   * Desabilita o `po-button` e no permite que o usurio interaja com o mesmo.
   *
   * @default `false`
   */
  @Input('p-disabled') set disabled(value: boolean) {
    this._disabled = <any>value === '' ? true : convertToBoolean(value);
  }
  get disabled(): boolean {
    return this._disabled;
  }

  /** Ao que ser executada quando o usurio clicar sobre o `po-button`. */
  @Output('p-click') click = new EventEmitter<null>();
}
