import { EventEmitter, Input, Output, Directive } from '@angular/core';
import { NgForm } from '@angular/forms';

import { convertToBoolean } from '../../../utils/util';

import { PoDynamicFormField } from './po-dynamic-form-field.interface';

/**
 *
 * @description
 *
 * Componente para criao de formulrios dinmicos a partir de uma lista de objetos.
 *
 * Tambm  possvel verificar se o formulrio est vlido e informar valores para a exibio de informaes.
 */
@Directive()
export class PoDynamicFormBaseComponent {
  private _groupForm?: boolean = false;

  /**
   * @optional
   *
   * @description
   *
   * Nome da propriedade, atribuda ao `PoDynamicFormField.property`, que iniciar o campo com foco.
   */
  @Input('p-auto-focus') autoFocus?: string;

  /**
   * @description
   *
   * Coleo de objetos que implementam a interface `PoDynamicFormField`, para definio dos campos que sero criados
   * dinamicamente.
   *
   * > Ex: `[ { property: 'name' } ]`
   *
   * Regras de tipagem e criao dos componentes:
   *
   * - Caso o *type* informado seja *boolean* o componente criado ser o `po-switch`.
   * - Caso o *type* informado seja *currency* e no seja informado um *mask* ou *pattern* o componente criado ser o `po-decimal`,
   * caso seja informado um *mask* ou *pattern* o componente criado ser o `po-input`.
   * - Caso o *type* informado seja *number* e no seja informado um *mask* ou *pattern* o componente criado ser o `po-number`, caso seja
   * informado um *mask* ou *pattern* o componente criado ser o `po-input`.
   * - Caso a lista possua a propriedade `options` e a mesma possua at 3 itens o componente criado ser o `po-radio-group`
   * ou `po-checkbox-group` se informar a propriedade `optionsMulti`.
   * - Caso a mesma possua 3 ou mais itens, ser criado o componente `po-select` ou, `po-multiselect` se a propriedade `optionsMulti`
   * for verdadeira.
   * - Caso o *type* informado seja *date* ou *datetime* o componente criado ser o `po-datepicker`.
   * - Caso seja informado a propriedade `optionsService` o componente criado ser o `po-combo`.
   * - Caso o *type* informado seja *time* o componente criado ser um `po-input` podendo receber um *mask* para formatar
   * o valor exibido, caso no seja informado um *mask* o componente ser criado com a mscara '99:99' por padro.
   * - Caso a lista possua a propriedade `rows` e esta seja definida com valor maior ou igual a 3 o componente criado ser
   * o `po-textarea`, caso o valor da propriedade `rows` seja menor que 3 o componente criado ser o `po-input`.
   * - Caso seja informada a propriedade `secret` o componente criado ser o `po-password`.
   * - Caso o *type* informado seja *string* o componente criado ser o `po-input`.
   *
   * @default `[]`
   */
  @Input('p-fields') fields: Array<PoDynamicFormField>;

  /**
   * Objeto que ser utilizado como valor para exibir as informaes, ser recuperado e preenchido atravs do atributo *property*
   * dos objetos contidos na propridade `p-fields`.
   *
   * Pode iniciar com valor ou apenas com um objeto vazio que ser preenchido conforme descrito acima.
   *
   * > Ex: `{ name: 'po' }`
   */
  @Input('p-value') value: any;

  /**
   * @optional
   *
   * @description
   *
   * Na inicializao do componente ser repassado o objeto de formulrio utilizado no componente,
   * podendo ser utilizado para validaes e/ou deteco de mudana dos valores.
   *
   * Portanto existem duas maneiras de recuperar o formulrio,
   * atravs de *template reference* e atravs do *output*, veja os exemplos abaixo:
   *
   * > *template reference*
   *
   * ```html
   *  <po-dynamic-form #dynamicForm>
   *  </po-dynamic-form>
   *
   *  <po-button p-label="Adicionar" [p-disabled]="dynamicForm?.form.invalid">
   *  </po-button>
   *
   * ```
   *
   * > *Output*
   *
   * ```html
   *  ...
   *  <po-dynamic-form (p-form)="getForm($event)">
   *  </po-dynamic-form>
   *
   *  <po-button p-label="Adicionar" [p-disabled]="dynamicForm?.invalid">
   *  </po-button>
   *  ...
   *
   * ```
   *
   * ```ts
   *  ...
   *
   *  export class AppComponent {
   *
   *    dynamicForm: NgForm;
   *
   *    getForm(form: NgForm) {
   *      this.dynamicForm = form;
   *    }
   *
   *  }
   * ```
   *
   * > Caso a propriedade `p-group-form` for verdadeira no ser repassado o formulrio, pois o mesmo utilizar
   * o formulrio pai.
   */
  @Output('p-form') formOutput: EventEmitter<NgForm> = new EventEmitter<NgForm>();

  /**
   * @optional
   *
   * @description
   * Ao informar esta propriedade, o componente passar a utilizar o formulrio pai para criar os `FormControl`
   * e com isso  possvel recuperar o valor do formulrio e suas validaes a partir do formulrio pai.
   *
   * ```html
   * <form #parentForm="ngForm">
   *
   *   <po-dynamic-form p-group-form [p-fields]="fields"></po-dynamic-form>
   *
   *  <po-button p-label="Adicionar" [p-disabled]="parentForm.invalid"></po-button>
   * </form>
   * ```
   */
  @Input('p-group-form') set groupForm(value: boolean) {
    this._groupForm = <any>value === '' ? true : convertToBoolean(value);
  }

  get groupForm(): boolean {
    return this._groupForm;
  }

  /**
   * Funo ou servio que ser executado na inicializao do componente.
   *
   * A propriedade aceita os seguintes tipos:
   * - `string`: *Endpoint* usado pelo componente para requisio via `POST`.
   * - `function`: Mtodo que ser executado.
   *
   * Ao ser executado, ir receber como parmetro o objeto informado no `p-value`.
   *
   * O retorno desta funo deve ser do tipo [PoDynamicFormLoad](documentation/po-dynamic-form#po-dynamic-form-load),
   * onde o usurio poder determinar as novas atualizaes dos campos, valores e determinar o campo a ser focado.
   *
   * Por exemplo:
   *
   * ```
   * onLoadFields(): PoDynamicFormLoad {
   *
   *   return {
   *     value: { cpf: undefined },
   *     fields: [
   *       { property: 'cpf' }
   *     ],
   *     focus: 'cpf'
   *   };
   * }
   *
   * ```
   * Para referenciar a sua funo utilize a propriedade `bind`, por exemplo:
   * ```
   *  [p-load]="onLoadFields.bind(this)"
   * ```
   */
  @Input('p-load') load?: string | Function;

  /**
   * Funo ou servio para validar as **mudanas do formulrio**.
   *
   * A propriedade aceita os seguintes tipos:
   * - `string`: *Endpoint* usado pelo componente para requisio via `POST`.
   * - `function`: Mtodo que ser executado.
   *
   * Ao ser executado, ir receber como parmetro um objeto com o nome da propriedade
   * alterada e o novo valor, conforme a interface `PoDynamicFormFieldChanged`:
   *
   * ```
   * { property: 'property name', value: 'new value' }
   * ```
   *
   * O retorno desta funo deve ser do tipo [PoDynamicFormValidation](documentation/po-dynamic-form#po-dynamic-form-validation),
   * onde o usurio poder determinar as novas atualizaes dos campos.
   * Por exemplo:
   *
   * ```
   * onChangeFields(changeValue): PoDynamicFormValidation {
   *
   * if (changeValue.property === 'state') {
   *
   *   return {
   *     value: { city: undefined },
   *     fields: [
   *       { property: 'city', options: this.getCity(changeValue.value.state) }
   *     ],
   *     focus: 'city'
   *   };
   * }
   *
   * ```
   * Para referenciar a sua funo utilize a propriedade `bind`, por exemplo:
   * ```
   *  [p-validate]="this.myFunction.bind(this)"
   * ```
   */
  @Input('p-validate') validate?: string | Function;
}
