/**
 * @docsPrivate
 *
 * @description
 *
 * Define os cones disponveis para o `po-tag`.
 */
export enum PoTagIcon {
  /** cone fechar. */
  Danger = 'po-icon-close',

  /** cone de informao. */
  Info = 'po-icon-info',

  /** cone que representa confirmao. */
  Success = 'po-icon-ok',

  /** cone com ponto de exclamao. */
  Warning = 'po-icon-warning'
}
