import { Input, EventEmitter, Directive } from '@angular/core';

import { convertToBoolean } from './../../utils/util';
import { PoModalAction } from './po-modal-action.interface';

/**
 * @description
 *
 * O componente `po-modal`  utilizado para incluir contedos rpidos e informativos.
 *
 * No cabealho do componente  possvel definir um ttulo e como tambm permite ocultar o cone de fechamento da modal.
 *
 * Em seu corpo  possvel definir um contedo informativo, podendo utilizar componentes como por exemplo `po-chart`,
 * `po-table` e os demais componentes do PO.
 *
 * No rodap encontram-se os botes de ao primria e secundria, no qual permitem definir uma ao e um rtulo, bem como
 * definir um estado de carregando e / ou desabilitado. Tambm  possvel definir o boto com o tipo *danger*.
 *
 * >  possvel fechar a modal atravs da tecla *ESC*, quando a propriedade `p-hide-close` no estiver habilitada.
 */
@Directive()
export class PoModalBaseComponent {
  private _hideClose?: boolean = false;
  private _size?: string = 'md';

  /** Ttulo da modal. */
  @Input('p-title') title: string;

  /**
   * Deve ser definido um objeto que implementa a interface `PoModalAction` contendo a label e a funo da primeira ao.
   * Caso esta propriedade no seja definida ou esteja incompleta, automaticamente ser adicionado um boto de ao com
   * a funo de fechar a modal.
   */
  @Input('p-primary-action') primaryAction?: PoModalAction;

  /** Deve ser definido um objeto que implementa a interface `PoModalAction` contendo a label e a funo da segunda ao. */
  @Input('p-secondary-action') secondaryAction?: PoModalAction;

  /**
   * Define o tamanho da modal.
   *
   * Valores vlidos:
   *  - `sm` (pequeno)
   *  - `md` (mdio)
   *  - `lg` (grande)
   *  - `xl` (extra grande)
   *  - `auto` (automtico)
   *
   * > Quando informado `auto` a modal calcular automaticamente seu tamanho baseado em seu contedo.
   * Caso no seja informado um valor, a modal ter o tamanho definido como `md`.
   *
   * > Todas as opes de tamanho possuem uma largura mxima de **768px**.
   */
  @Input('p-size') set size(value: string) {
    const sizes = ['sm', 'md', 'lg', 'xl', 'auto'];
    this._size = sizes.indexOf(value) > -1 ? value : 'md';
  }

  get size() {
    return this._size;
  }

  /**
   * Define o fechamento da modal ao clicar fora da mesma.
   * Informe o valor `true` para ativar o fechamento ao clicar fora da modal.
   */
  clickOut?: boolean = false;
  @Input('p-click-out') set setClickOut(value: boolean | string) {
    this.clickOut = value === '' ? false : convertToBoolean(value);
  }

  /**
   * @optional
   *
   * @description
   *
   * Oculta o cone de fechar do cabealho da modal.
   *
   * > Caso a propriedade estiver habilitada, no ser possvel fechar a modal atravs da tecla *ESC*.
   *
   * @default `false`
   */
  @Input('p-hide-close') set hideClose(value: boolean) {
    this._hideClose = convertToBoolean(value);
  }

  get hideClose() {
    return this._hideClose;
  }

  // Controla se a modal fica oculto ou visvel, por padro  oculto
  isHidden = true;

  // Event emmiter para quando a modal  fechada pelo 'X'.
  public onXClosed = new EventEmitter<boolean>();

  /** Funo para fechar a modal. */
  close(xClosed = false): void {
    this.isHidden = true;
    if (xClosed) {
      this.onXClosed.emit(xClosed);
    }
  }

  /** Funo para abrir a modal. */
  open(): void {
    this.validPrimaryAction();

    this.isHidden = false;
  }

  validPrimaryAction() {
    if (!this.primaryAction) {
      this.primaryAction = {
        action: () => this.close(),
        label: 'Ok'
      };
    }

    if (!this.primaryAction['action']) {
      this.primaryAction['action'] = () => this.close();
    }
    if (!this.primaryAction['label']) {
      this.primaryAction['label'] = 'Ok';
    }
  }
}
