import { Component, Input } from '@angular/core';

import { convertToBoolean } from '../../../utils/util';

/**
 * @docsPrivate
 *
 * @description
 *
 * Componente que exibe um cone de carregamento de contedo. A cor padro para ele  a primria conforme o tema utilizado.
 *  possvel alter-la para um tom cinza conforme a necessidade.
 */
@Component({
  selector: 'po-loading-icon',
  templateUrl: 'po-loading-icon.component.html'
})
export class PoLoadingIconComponent {
  private _neutralColor: boolean;

  /**
   * @optional
   *
   * @description
   *
   * Definio para cor neutra (cinza) para o cone de carregamento.
   *
   * @default `false`
   */
  @Input('p-neutral-color') set neutralColor(value: boolean) {
    this._neutralColor = convertToBoolean(value);
  }

  get neutralColor(): boolean {
    return this._neutralColor;
  }
}
