/**
 * @usedBy PoDatepickerComponent
 *
 * @description
 *
 * *Enum* que define o padro de formatao das datas.
 *
 * > Caso um formato padro seja definido, o mesmo no ser mais alterado de acordo com o formato de entrada.
 */
export enum PoDatepickerIsoFormat {
  /** Padro **E8601DAw** (*yyyy-mm-dd*). */
  Basic = 'basic',

  /** Padro **E8601DZw** (*yyyy-mm-ddThh:mm:ss+|-hh:mm*). */
  Extended = 'extended'
}
