import { PoToasterOrientation } from './po-toaster-orientation.enum';
import { PoToasterType } from './po-toaster-type.enum';
import { PoToaster } from './po-toaster.interface';

/**
 * @docsPrivate
 *
 * @description
 *
 * O componente po-toaster foi construdo para exibir notificaes para o usurio.
 */
export abstract class PoToasterBaseComponent {
  /** Disponibiliza uma ao para a notificao. */
  action: Function;

  /** Permite alterar o label do boto quando houver uma ao definida. */
  actionLabel: string;

  /** Mensagem a ser exibida na notificao. */
  message: string;

  /** Orientao da notificao, a mesma pode ser exibida na parte superior ou inferior da pgina. */
  orientation: PoToasterOrientation = PoToasterOrientation.Bottom;

  /** ComponentRef */
  componentRef: any;

  /** Posio para notificao aparecer na tela. */
  position: number;

  /** Tipo de notificao. */
  type: PoToasterType;

  /** Fecha a notificao. */
  abstract close(): void;

  /**
   * Altera a posio da notificao.
   * @param number value
   */
  abstract changePosition(value: number): void;

  /**
   * Configura o componente po-toaster de acordo com as definies do usurio.
   * @param PoToaster poToaster
   * @param ComponentRef comp
   */
  abstract configToaster(poToaster: PoToaster): void;
}
