import { Injectable } from '@angular/core';

import { getBrowserLanguage, getShortLanguage, isLanguage, poLocaleDefault, poLocales } from '../../utils/util';

const poDefaultLanguage = 'PO_DEFAULT_LANGUAGE';
const poLocaleKey = 'PO_USER_LOCALE';

/**
 * @docsPrivate
 *
 * @description
 *
 * Servio responsvel por gerenciar as linguagens da aplicao.
 */
@Injectable({
  providedIn: 'root'
})
export class PoLanguageService {
  set languageDefault(language: string) {
    if (language && isLanguage(language)) {
      localStorage.setItem(poDefaultLanguage, language);
    }
  }

  get languageDefault(): string {
    return localStorage.getItem(poDefaultLanguage);
  }

  /**
   * @description
   *
   * Mtodo responsvel por retornar o idioma ativo.
   *
   * A busca do idioma ser feita na seguinte ordem:
   *
   *   1 - o idioma que foi armazenado no *localStorage*, atravs do mtodo `setLanguage()` utilizado pelo i18n.
   *
   *   2 - o valor inserido nas configuraes do mdulo do i18n atravs do parmetro `config`, sendo o idioma inserido
   * na propriedade `language` da interface `PoI18nConfigDefault`.
   *
   *   3 - o idioma do navegador utilizado.
   *
   * > Caso o idioma do navegador no seja suportado pelo PO (`pt`, `en`, `es` ou `ru`), ser retornado valor `pt`.
   *
   * **Retorno:** `string` com a sigla do idioma ativo.
   */
  getLanguage(): string {
    const language = localStorage.getItem(poLocaleKey) || this.languageDefault || getBrowserLanguage();

    return language && language.toLowerCase();
  }

  /**
   * @description
   *
   * Mtodo responsvel por retornar o idioma *default* da aplicao definido nas configuraes do mdulo do i18n atravs
   * do parmetro `config`.
   *
   * **Retorno:** `string` com a sigla do idioma *default*.
   */
  getLanguageDefault(): string {
    return this.languageDefault;
  }

  /**
   * @description
   *
   * Mtodo responsvel por retornar a abreviao do idioma ativo na aplicao.
   *
   * @default `pt`
   *
   * **Retorno:** `string` com a sigla abreviada do idioma ativo.
   */
  getShortLanguage(): string {
    const language = this.getLanguage();
    const shortLanguage = getShortLanguage(language);

    return poLocales.includes(shortLanguage) ? shortLanguage : poLocaleDefault;
  }

  /**
   * @description
   *
   * Mtodo para salvar o idioma da aplicao no *storage*, utilizado pelo servio do i18n.
   *
   * > Ao definir um idioma por este mtodo, todos os mdulos da aplicao utilizaro o idioma definido.
   *
   * @param language sigla do idioma.
   *
   * Esta sigla deve ser composta por duas letras representando o idioma,
   * podendo ser adicionado outras duas letras representando o pas, por exemplo: `pt`, `pt-BR`, `pt-br`, `en` ou `en-US`.
   *
   * > Caso seja informado um valor diferente deste padro, o mesmo ser ignorado.
   */
  setLanguage(language: string): void {
    if (!isLanguage(language)) {
      return;
    }

    localStorage.setItem(poLocaleKey, language.toLowerCase());
  }

  /**
   * @description
   *
   * Mtodo que define o idioma configurado a partir do parmetro `config` utilizado pelo mdulo do i18n.
   *
   * > Ao definir um idioma por este servio, apenas o mdulo do i18n referente a esta configurao utilizar o idioma definido.
   *
   * @param language sigla do idioma.
   *
   * Esta sigla deve ser composta por duas letras representando o idioma,
   * podendo ser adicionado outras duas letras representando o pas, por exemplo: `pt`, `pt-BR`, `pt-br`, `en` ou `en-US`.
   *
   * > Caso seja informado um valor diferente deste padro, o mesmo ser ignorado.
   */
  setLanguageDefault(language: string): void {
    this.languageDefault = language;
  }
}
