import { AbstractControl, ControlValueAccessor, Validator } from '@angular/forms';
import { EventEmitter, Input, Output, Directive } from '@angular/core';

import { convertToBoolean } from '../../../utils/util';
import { requiredFailed } from '../validators';
import { InputBoolean } from '../../../decorators';

/**
 * @description
 *
 * O componente `po-rich-text`  um editor de textos enriquecidos.
 *
 * Para edio de texto simples sem formatao recomenda-se o uso do componente [**po-textarea**](/documentation/po-textarea).
 *
 * > No navegador Internet Explorer no  possvel alterar a cor do texto.
 */
@Directive()
export abstract class PoRichTextBaseComponent implements ControlValueAccessor, Validator {
  private _height?: number;
  private _placeholder: string;
  private _readonly: boolean;
  private _required: boolean;

  invalid: boolean = false;
  onChangeModel: any = null;
  value: string;

  // tslint:disable-next-line
  private onTouched: any = null;
  private validatorChange: any;

  /**
   * @optional
   *
   * @description
   *
   * Aplica foco no elemento ao ser iniciado.
   *
   * > Caso mais de um elemento seja configurado com essa propriedade, apenas o ltimo elemento declarado com ela ter o foco.
   *
   * @default `false`
   */
  @Input('p-auto-focus') @InputBoolean() autoFocus: boolean = false;

  /**
   * @description
   *
   * Mensagem que ser apresentada quando a propriedade required estiver habilitada e o campo for limpo aps algo ser digitado.
   */
  @Input('p-error-message') errorMessage?: string = '';

  /**
   * @optional
   *
   * @description
   *
   * Define a altura da rea de edio de texto.
   *
   * > Altura mnima do componente  `94` e a altura mxima  `262`.
   */
  @Input('p-height') set height(height: number) {
    this._height = height;
  }

  get height() {
    return this._height;
  }

  /**
   * @optional
   *
   * @description
   *
   * Texto de apoio do campo.
   */
  @Input('p-help') help?: string;

  /**
   * @optional
   *
   * @description
   *
   * Rtulo do campo.
   */
  @Input('p-label') label?: string;

  /** Nome e identificador do campo. */
  @Input('name') name: string;

  /**
   * @optional
   *
   * @description
   *
   * Define se a indicao de campo opcional ser exibida.
   *
   * > No ser exibida a indicao se:
   * - O campo conter `p-required`;
   * - No possuir `p-help` e/ou `p-label`.
   *
   * @default `false`
   */
  @Input('p-optional') optional: boolean;

  /**
   * @optional
   *
   * @description
   *
   * Mensagem que aparecer enquanto o campo no estiver preenchido.
   *
   * @default ''
   */
  @Input('p-placeholder') set placeholder(value: string) {
    this._placeholder = value || '';
  }

  get placeholder() {
    return this._placeholder;
  }

  /**
   * @optional
   *
   * @description
   *
   * Indica que o campo ser somente leitura.
   *
   * @default `false`
   */
  @Input('p-readonly') set readonly(value: boolean) {
    this._readonly = convertToBoolean(value);
  }

  get readonly() {
    return this._readonly;
  }

  /**
   * @optional
   *
   * @description
   *
   * Indica que o campo ser obrigatrio.
   *
   * @default `false`
   */
  @Input('p-required') set required(value: boolean) {
    this._required = convertToBoolean(value);

    this.validateModel(this.value);
  }

  get required() {
    return this._required;
  }

  /** Evento disparado ao deixar o campo e que recebe como parmetro o valor alterado. */
  @Output('p-change') change?: EventEmitter<any> = new EventEmitter();

  /** Evento disparado ao modificar valor do model e que recebe como parmetro o valor alterado. */
  @Output('p-change-model') changeModel?: EventEmitter<any> = new EventEmitter();

  // Funo implementada do ControlValueAccessor
  // Usada para interceptar as mudanas e no atualizar automaticamente o Model
  registerOnChange(func: any): void {
    this.onChangeModel = func;
  }

  // Funo implementada do ControlValueAccessor
  // Usada para interceptar as mudanas e no atualizar automaticamente o Model
  registerOnTouched(func: any): void {
    this.onTouched = func;
  }

  registerOnValidatorChange(fn: () => void) {
    this.validatorChange = fn;
  }

  validate(abstractControl: AbstractControl): { [key: string]: any } {
    if (requiredFailed(this.required, false, abstractControl.value)) {
      return {
        required: {
          valid: false
        }
      };
    }
  }

  writeValue(value: string): void {
    this.value = value;
  }

  // Executa a funo onChange
  protected updateModel(value: any) {
    // Quando o rich-text no possui um formulrio, ento esta funo no  registrada
    if (this.onChangeModel) {
      this.onChangeModel(value);
    }
  }

  protected validateModel(value: any) {
    if (this.validatorChange) {
      this.validatorChange(value);
    }
  }
}
