import { AfterContentInit, Component, ElementRef, Input } from '@angular/core';
import { Observable } from 'rxjs';

import { uuid } from '../../../utils/util';

import { PoStepperStatus } from '../enums/po-stepper-status.enum';

/**
 * @description
 *
 * O componente `po-step`  utilizado para envolver e renderizar o contedo de um passo (*step*) do `po-stepper`, por exemplo:
 *
 * ```
 * <po-stepper>
 *
 *    <po-step p-label="Endereo">
 *
 *        <!-- Contedo referente ao endereo -->
 *
 *    </po-step>
 *
 *    <po-step p-label="Pagamento">
 *
 *        <!-- Contedo referente ao pagamento -->
 *
 *    </po-step>
 *
 *  </po-stepper>
 * ```
 *
 * A renderizao do contedo envolvido na tela e o controle dos status so feitos automaticamente. No qual, o primeiro
 * `po-step` encontrado ser colocado como ativo, o prximo fica com o status *default* e os demais ficam
 * desabilitados (*disabled*).
 *
 * Ao clicar no `po-step` que est com o status *default*, o que est ativo ficar com o
 * status de concludo (*done*) e o prximo que estava desabilitado ficar com o status *default* e o restante permanecer
 * com o status desabilitado.
 *
 * > Ao utilizar o `po-step`, o componente `po-stepper` funcionar de forma sequencial, ou seja, no ser possvel
 * pular para outro `po-step` que esteja com o status igual a desabilitado (*disabled*).
 *
 * Acesse a [documentao do `po-stepper`](/documentation/po-stepper) para ter mais informaes sobre o seu funcionamento
 * e exemplos de uso.
 */
@Component({
  selector: 'po-step',
  templateUrl: 'po-step.component.html'
})
export class PoStepComponent implements AfterContentInit {
  private _status?: PoStepperStatus = PoStepperStatus.Disabled;

  // ID do step
  id?: string = uuid();

  /**
   * @optional
   *
   * @description
   *
   * Funo chamada quando o prximo *step* for clicado ou quando o mtodo `PoStepperComponent.next()` for chamado.
   * Ao retornar `true` define que esse *step* ficar ativo e o atual como concludo (*done*).
   * Tambm aceita funes que retornem `Observable<boolean>`. Ao retornar um `Observable<boolean>`,
   * garanta que esse `Observable` ser completado, caso houver algum erro durante o processo no ser possvel prosseguir
   * ao prximo *step*.
   *
   * Ao ser disparada, a mesma receber por parmetro o `PoStepComponent` atual.
   *
   * O contexto da funo que ser chamada, ser o mesmo que o do `PoStepComponent`, ento para poder alterar
   * para o contexto do componente que o est utilizando, pode ser utilizado a propriedade `bind` do Javascript.
   * Por exemplo, para a funo `validate`:
   *
   * ```
   * <po-step p-label="Step 1" [p-can-active-next-step]="validate.bind(this)">
   * ...
   * </po-step>
   * ```
   */
  @Input('p-can-active-next-step') canActiveNextStep:
    | ((currentStep) => boolean)
    | ((currentStep) => Observable<boolean>);

  /** Ttulo que ser exibido descrevendo o passo (*step*). */
  @Input('p-label') label: string;

  // Estado de exibio do `po-step`.
  @Input('p-status') set status(status: PoStepperStatus) {
    this._status = status;

    this.setDisplayOnActiveOrError();
  }

  get status() {
    return this._status;
  }

  constructor(private elementRef: ElementRef) {}

  ngAfterContentInit() {
    this.setDisplayOnActiveOrError();
  }

  protected setDisplayOnActiveOrError() {
    this.elementRef.nativeElement.style.display =
      this.status === PoStepperStatus.Active || this.status === PoStepperStatus.Error ? '' : 'none';
  }
}
