import { Directive, Input, TemplateRef } from '@angular/core';

/**
 * @usedBy PoTableComponent
 *
 * @description
 *
 * Esta diretiva permite que seja apresentada informaes adicionais a respeito de uma determinada linha de
 * dados, de forma que possam ser exibidas e ocultadas atravs do boto de acionamento.
 *
 * > Quando utilizada, sobrepe as funcionalidade bsicas do *master-detail*.
 *
 * Em seu uso, deve-se utilizar como parmetro a referncia da _linha_ e/ou _ndice_, sendo por padro linha. Caso no seja declarado,
 * o componente no exibir contedo.
 *  - Linha: `row` determina o item da linha corrente.
 *  - ndice: `rowIndex` determina o ndice da linha corrente.
 *
 * Esta diretiva compe-se de dois meios para uso, de forma explcita tal como em *syntax sugar*. Veja a seguir ambos, respectivamente:
 *
 * ```
 * ...
 * <po-table
 *   [p-columns]="columns"
 *   [p-items]="items">
 *     <ng-template p-table-row-template let-rowItem let-i="rowIndex" [p-table-row-template-show]="isShow">
 *       <detail-row [row]="rowItem"></detail-row>
 *     </ng-template>
 * ...
 * ```
 *
 * ```
 * ...
 * <po-table
 *   [p-columns]="columns"
 *   [p-items]="items">
 *     <div *p-table-row-template="let rowItem, let i=rowIndex">
 *       <detail-row [row]="rowItem"></detail-row>
 *     </div>
 * ...
 *
 * ```
 *
 * A diretiva **p-table-row-template**, possibilita tambm que determinada linha apresente ou no seu _template_. Para isto,
 *  necessrio atribuir a referncia da funo que faz esta verificao,  propriedade `p-table-row-template-show`,
 * a mesma deve retornar um valor do tipo *boolean*. Veja o exemplo a seguir:
 *
 * ```
 * ...
 * @Component({
 *    selector: 'app-root',
 *    templateUrl: `
 *      ...
 *      <po-table
 *        [p-columns]="columns"
 *        [p-items]="items">
 *          <ng-template p-table-row-template let-rowItem let-i="rowIndex" [p-table-row-template-show]="isUndelivered">
 *            <detail-row [row]="rowItem"> </detail-row>
 *          </div>
 *      ...
 *    `
 * })
 * export class AppComponent {
 *    public dataTable = [{
 *      code: 1200,
 *      product: 'Rice',
 *      costumer: 'Supermarket 1',
 *      quantity: 3,
 *      status: 'delivered',
 *      license_plate: 'MDJD9191',
 *      batch_product: 18041822,
 *      driver: 'Jos Oliveira'
 *    }, {
 *      code: 1355,
 *      product: 'Bean',
 *      costumer: 'Supermarket 2',
 *      quantity: 1,
 *      status: 'transport',
 *      license_plate: 'XXA5454',
 *      batch_product: 18041821,
 *      driver: 'Francisco Pereira'
 *    }];
 *
 *    isUndelivered(row, index: number) {
 *      return row.status !== 'delivered';
 *    }
 * }
 * ```
 * > No exemplo acima, somente ser disponibilizado os detalhes de informaes nas linhas cujo o valor de `status`
 * no correspondam  *delivered*.
 */
@Directive({
  selector: '[p-table-row-template]'
})
export class PoTableRowTemplateDirective {
  /**
   * @optional
   *
   * @description
   *
   * Funo que deve retornar um valor booleano, informando se apresentar o template da linha.
   * Sero passados o objeto da linha e o indice da mesma por parmetro, para que seja possvel
   * tomar uma deciso a partir do objeto da linha corrente.
   *
   * Caso no ser utilizado esta propriedade, sero apresentados todos os detalhes das linhas.
   *
   * @default `true`
   */
  @Input('p-table-row-template-show') poTableRowTemplateShow: (row: any, index: number) => boolean;

  // Necessrio manter templateRef para o funcionamento do row template.
  constructor(public templateRef: TemplateRef<any>) {}
}
