import { EventEmitter, Input, Output, Directive } from '@angular/core';

import { convertToBoolean } from '../../utils/util';

import { PoStepComponent } from './po-step/po-step.component';
import { PoStepperItem } from './po-stepper-item.interface';
import { PoStepperOrientation } from './enums/po-stepper-orientation.enum';
import { PoStepperStatus } from './enums/po-stepper-status.enum';

const poStepperOrientationDefault = PoStepperOrientation.Horizontal;

/**
 * @description
 *
 * O `po-stepper` permite que um processo seja dividido em passos para que o usurio o realize
 * mais facilmente.
 *
 * Existem duas formas de utilizao:
 *
 * 1 - Usando o componente [**po-step**](/documentation/po-step) para renderizar e descrever os passos.
 *
 * 2 - Atravs da propriedade `p-steps` para descrever os passos do processo, sendo responsabilidade do desenvolvedor o controle
 * de renderizao do que ser exibido a cada *step* ativo.
 *
 * Atravs de suas propriedades,  possvel definir se sua orientao ser horizontal ou vertical,
 * alm da possibilidade de aumentar o tamanho dos *steps*.
 *
 * Tambm  possvel navegar entre os *steps* atravs do teclado utilizando a tecla *tab* e, para ativar o *step* em foco basta
 * pressionar a tecla *enter*. Alm disso,  possvel ativar a exibio de cones no lugar de nmeros nos *steps* atravs da
 * propriedade [`p-step-icons`](/documentation/po-stepper#stepIconsProperty).
 *
 * #### Utilizando os mtodos do componente:
 *
 * Para acessar os mtodos do componente  necessrio ter a referncia do mesmo.
 *
 * Por exemplo, utilizando um [**ViewChild**](https://angular.io/api/core/ViewChild):
 *
 * ```
 * @ViewChild(PoStepperComponent) poStepperComponent: PoStepperComponent;
 * ```
 *
 * E para acessar o mtodo:
 *
 * ```
 * poStepperComponent.next();
 * ```
 *
 * #### Boas prticas
 *
 * - Evite `labels` extensos que quebram o layout do `po-stepper`, use `labels` diretos, curtos e intuitivos.
 * - Utilize apenas um `po-stepper` por pgina.
 */
@Directive()
export class PoStepperBaseComponent {
  private _orientation?: PoStepperOrientation = poStepperOrientationDefault;
  private _sequential?: boolean = true;
  private _step: number = 1;
  private _steps: Array<PoStepperItem> = [];

  /**
   * @optional
   *
   * @description
   *
   * Define a orientao de exibio do `po-stepper`.
   *
   * > Veja os valores vlidos no *enum* [PoStepperOrientation](documentation/po-stepper#stepperOrientation).
   *
   * @default `PoStepperOrientation.Horizontal`
   */
  @Input('p-orientation') set orientation(value: PoStepperOrientation) {
    this._orientation = (<any>Object).values(PoStepperOrientation).includes(value)
      ? value
      : poStepperOrientationDefault;
  }

  get orientation(): PoStepperOrientation {
    return this._orientation;
  }

  /**
   * @optional
   *
   * @description
   *
   * Controla o passo atual do `po-stepper`.
   *
   * > Ao utilizar esta propriedade e tambm utilizar o componente [**po-step**](/documentation/po-step),
   * o valor desta propriedade ser ignorada permanecendo a definio do [**po-step**](/documentation/po-step).
   *
   * @default `1`
   */
  @Input('p-step') set step(step: number) {
    if (step >= 1 && step <= this.steps.length) {
      this._step = step;
      this._steps[this._step - 1].status = PoStepperStatus.Active;
    }
  }

  get step(): number {
    return this._step;
  }

  /**
   * @optional
   *
   * @description
   *
   * Lista dos itens do stepper. Se o valor estiver indefinido ou invlido, ser inicializado como um array vazio.
   *
   * > Ao utilizar esta propriedade e tambm utilizar o componente [**po-step**](/documentation/po-step),
   * o valor desta propriedade ser ignorada permanecendo a definio do [**po-step**](/documentation/po-step).
   */
  @Input('p-steps') set steps(steps: Array<PoStepperItem>) {
    this._steps = Array.isArray(steps) ? steps : [];
    this._steps.forEach(step => (step.status = PoStepperStatus.Default));
    this.step = 1;
  }

  get steps(): Array<PoStepperItem> {
    return this._steps;
  }

  /**
   *
   * @optional
   *
   * @description
   *
   * <a id="stepIconsProperty"></a>
   *
   * Habilita a exibio de cone ao invs de nmero no centro do crculo dos *steps*.
   *
   * @default `false`
   */
  @Input('p-step-icons') stepIcons: boolean;

  /**
   * @optional
   *
   * @description
   *
   * Define o tamanho dos *steps* em *pixels*, possibilitando um maior destaque.
   *
   * O valor informado deve ser entre `24` e `64`.
   *
   * > Valores que no se enquadrarem a esta regra sero ignorados, mantendo-se o valor *default*.
   *
   * @default `24`
   */
  @Input('p-step-size') stepSize: number;

  /**
   * @optional
   *
   * @description
   *
   * Define se o `po-stepper` ser sequencial ou aleatrio.
   *
   * > Ao utilizar o componente [**po-step**](/documentation/po-step), o valor desta propriedade sempre ser verdadeiro.
   *
   * @default `true`
   */
  @Input('p-sequential') set sequential(sequential: boolean) {
    this._sequential = convertToBoolean(sequential);
  }

  get sequential(): boolean {
    return this._sequential;
  }

  /** Ao que ser executada quando o usurio mudar o passo do `po-stepper`. */
  @Output('p-change-step') onChangeStep = new EventEmitter<number | PoStepComponent>();
}
