import { HttpClient, HttpClientModule } from '@angular/common/http';
import { APP_INITIALIZER, ModuleWithProviders, NgModule } from '@angular/core';

import { PoLanguageService } from './../po-language/po-language.service';

import { I18N_CONFIG } from './po-i18n-config-injection-token';
import { returnPoI18nService, PoI18nService } from './po-i18n.service';
import { PoI18nConfig } from './interfaces/po-i18n-config.interface';
import { PoLanguageModule } from '../po-language/po-language.module';

/**
 * @description
 *
 * Mdulo do servio `PoI18nService` para controle de idiomas com PO.
 *
 * Para utilizao do servio de idiomas `PoI18nService`, deve-se importar este mdulo mesmo j havendo importado
 * o mdulo `PoModule`. Na importao deve ser invocado o mtodo `config`, informando um objeto que deve implementar
 * a interface [`PoI18nConfig`](documentation/po-i18n#poI18nConfig) para configurao.
 *
 * <a id="i18n-config"></a>
 * **Exemplo de configurao do mdulo do i18n:**
 * ```
 * import { PoI18nConfig } from '@po-ui/ng-components';
 *
 * import { generalEn } from './i18n/general-en';
 * import { generalPt } from './i18n/general-pt';
 *
 * const i18nConfig: PoI18nConfig = {
 *   default: {
 *     language: 'pt-BR',
 *     context: 'general',
 *     cache: true
 *   },
 *   contexts: {
 *     general: {
 *       'pt-BR': generalPt,
 *       'en-US': generalEn
 *     },
 *     hcm: {
 *       url: 'http://10.1.1.1/api/translations/hcm/'
 *     }
 *   }
 * };
 *
 * @NgModule({
 *   declarations: [],
 *   imports: [
 *     PoModule,
 *     PoI18nModule.config(i18nConfig)
 *   ],
 *   bootstrap: [AppComponent]
 * })
 * ```
 *
 * Para cada contexto  possvel definir a origem das literais, que podem ser de um servio REST ou
 * de um objeto. Exemplo:
 *
 * Arquivo general-pt.ts
 * ```
 * export const generalPt = {
 *  add: 'Adicionar',
 *  greeting: 'Prazer, {0} {1}',
 *  people: '{0} Pessoas,
 *  remove: 'Remover'
 * }
 * ```
 *
 * Arquivo general-en.ts
 * ```
 * export const generalEn = {
 *  add: 'Add',
 *  greeting: 'Nice to meet you, {0} {1}',
 *  people: '{0} People,
 *  remove: 'Remove'
 * }
 * ```
 *
 * **Exemplo de configurao de contextos usando constantes externas:**
 * ```
 * import { PoI18nConfig } from '@po-ui/ng-components';
 *
 * import { generalEn } from './i18n/general-en';
 * import { generalPt } from './i18n/general-pt';
 *
 * const i18nConfig: PoI18nConfig = {
 *   contexts: {
 *     general: {
 *       'pt-BR': generalPt, // constantes em arquivos separados
 *       'en-US': generalEn // constantes em arquivos separados
 *     },
 *     crm: {
 *       url: 'http://10.0.0.1:3000/api/translations/crm'
 *     }
 *   },
 *   default: {}
 * }
 * ```
 *
 * **Exemplo de configurao de um contexto utilizando servio:**
 *
 * Ao optar por utilizar um servio para configurao de contexto, dever ser definida a URL
 * especfica do contexto, como nos exemplos abaixo:
 *
 *  - http://10.0.0.1:3000/api/translations/crm
 *  - http://10.0.0.1:3000/api/translations/general
 *
 * Os idiomas e literais sero automaticamente buscados com parmetros na prpria URL:
 * - **language**: o idioma ser sempre passado por parmetro e  recomendado utilizar uma das linguagens
 * suportadas pelo PO (`pt-br`, `en-us`, `es-es` ou `ru`).
 * - **literals**: as literais sero separadas por vrgula. Caso esse parmetro no seja informado, o
 * servio deve retornar todas as literais do idioma.
 *
 * Exemplos de requisio:
 *
 *  - http://10.0.0.1:3000/api/translations/crm?language=pt-br
 *  - http://10.0.0.1:3000/api/translations/crm?language=pt-br&literals=add,remove,text
 *
 * > Sempre que o idioma solicitado no for encontrado, ser buscado por `pt-br`.
 *
 * Alm dos contextos,  possvel definir as configuraes *default* do sistema na configurao do
 * mdulo utilizando a interface [`PoI18nConfig`](documentation/po-i18n#poI18nConfig):
 *
 * **Exemplo de padres definidos:**
 * ```
 * const i18nConfig: PoI18nConfig = {
 *   contexts: {
 *     general: { }
 *   },
 *   default: {
 *    language: 'pt-BR',
 *    context: 'general',
 *    cache: true
 *   }
 * }
 * ```
 *
 * **Importante:**
 *
 * Recomenda-se que as definies *default* sejam realizadas apenas uma vez na aplicao,
 * preferencialmente no mdulo `AppModule`.
 *
 * **i18n com *Lazy loading***
 *
 * Para aplicaes que utilizem a abordagem de mdulos com carregamento *lazy loading*, caso seja
 * definida outra configurao do `PoI18nModule`, deve-se atentar os seguintes detalhes:
 *
 * - Caso existam literais comuns na aplicao, estas devem ser reimportadas;
 * - No defina outra *default language* para este mdulo. Caso for definida, ser sobreposta para
 * toda a aplicao;
 * - Caso precise de mdulos carregados via *lazy loading* com linguagens diferentes, utilize o
 * mtodo [`setLanguage()`](documentation/po-i18n#setLanguage) disponibilizado pelo `PoI18nService`
 * para definir a linguagem da aplicao e dos mdulos com as linguagens diferentes.
 */

@NgModule({
  imports: [HttpClientModule, PoLanguageModule]
})
export class PoI18nModule {
  static config(config: PoI18nConfig): ModuleWithProviders<PoI18nModule> {
    return {
      ngModule: PoI18nModule,
      providers: [
        {
          provide: I18N_CONFIG,
          useValue: config
        },
        {
          provide: APP_INITIALIZER,
          useFactory: initializeLanguageDefault,
          multi: true,
          deps: [I18N_CONFIG, PoLanguageService]
        },
        {
          provide: PoI18nService,
          useFactory: returnPoI18nService,
          deps: [I18N_CONFIG, HttpClient, PoLanguageService]
        }
      ]
    };
  }
}

export function initializeLanguageDefault(config: PoI18nConfig, languageService: PoLanguageService) {
  // tslint:disable-next-line:prefer-immediate-return
  const setDefaultLanguage = () => {
    if (config.default.language) {
      languageService.setLanguageDefault(config.default.language);
    }
  };
  return setDefaultLanguage;
}
